import React, { Suspense, useEffect, useState } from "react";
import { Navigate, Route, Routes } from "react-router-dom";
import AppBackdrop from "../components/Appdrop/AppBackdrop";
import { paths } from "./paths";
// import Data from "../json/Data.json";
import { Machine } from "../Interfaces/Interface";

// import HeaderBar from '../components/Header/HeaderBar';

const HeaderBar = React.lazy(() => import("../components/Header/HeaderBar"));
const Machines = React.lazy(
  () => import("../components/AttendanceMachines/Machines")
);
const Logs = React.lazy(() => import("../components/Logs/Logs"));
const Test = React.lazy(() => import("../components/test/Test"));

const Radio = React.lazy(() => import("../components/Reports/RadioSelect"));
const Controllers = React.lazy(
  () => import("../components/Controllers/AllControllers")
);
const CreateController = React.lazy(
  () => import("../components/Controllers/CreateController")
);

// const data=Data.Data;
const AppRoutes = () => {
  const [data, setData] = useState<Machine[]>([]);
  const [controllers, setControllers] = useState([]);
  const host: string = process.env.REACT_APP_API_ENDPOINT ?? "";

  const fetchControllers = async () => {
    try {
      const response = await fetch(host + "/Controllers");
      if (!response.ok) {
        throw new Error("Network response was not ok");
      }
      const result = await response.json();
      setControllers(result.Data);
      // console.log(result.Data)
    } catch (error) {
      console.error("Error fetching data:", error);
    }
  };

  const fetchData = async () => {
    try {
      // const username = "Kunal";
      // const password = "Kunal@2003";
      // const basicAuth = "Basic " + btoa(username + ":" + password);
      const response = await fetch(host + "/Machines")
      // , 
      // {
      //   headers: {
      //     Authorization: basicAuth,
      //     // 'Content-Type': 'application/json',
      //   },
      // }
      // );
        // console.log(response)
      

      if (!response.ok) {
        setData([]);
        throw new Error("Network response was not ok");
      }

      const result = await response.json();
    
      setData(result.Data);
    } catch (error) {
      setData([]);
      // console.error('Error fetching data:', error);
    }
  };
  const fetchDataWithTimeout = () => {
    // Fetch data immediately
    fetchData();

    // Set a timeout to fetch data every 5 seconds
    setTimeout(() => {
      fetchDataWithTimeout();
    }, 2000);
  };

  useEffect(() => {
    fetchControllers();
    fetchDataWithTimeout()
  }, []);

  return (
    <Suspense fallback={<AppBackdrop show />}>
      <Routes>
        <Route
          id="Dashboard"
          path={paths.DASHBOARD}
          element={
            <div>
              <HeaderBar name="Dashboard" subtitle="" />
            </div>
          }
        />

        <Route
          id="Attendance"
          path={paths.ATTENDANCE}
          element={
            <div>
              <HeaderBar name="Attendance System" subtitle="" />
              <Machines MachinesInfo={data} />
            </div>
          }
        />
        <Route
          id="test"
          path={paths.TEST}
          element={
            <div>
              <HeaderBar name="Test" subtitle="" />
              {/* <Logs name="test" subtitle="hello" /> */}
              {/* <CreateController /> */}
              <Test/>
            </div>
          }
        />

        <Route
          id="reports"
          path={paths.ATTENDANCEREPORTS}
          element={
            <div>
              <HeaderBar name="Attendance System  > reports " subtitle="" />
              <Radio MachinesData={data} />
            </div>
          }
        />

        <Route
          id="controllers"
          path={paths.CONTROLLERS}
          element={
            <div>
              <HeaderBar name="Controllers " subtitle="( Not Deleted )" />
              <Controllers ControllerRows={controllers} />
            </div>
          }
        />
      </Routes>
    </Suspense>
  );
};

export default AppRoutes;
